.hero-home {
  display: block;
  position: relative;
  background: url("../assets/Home_Pic.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
  min-height: 80vh;
  overflow: hidden;
}

.gradient-over {
  display: block;
  width: 100%;
  height: 102%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
  background: linear-gradient(160deg, #0A0A0A, rgba(0, 0, 0, 0), #0A0A0A);
  opacity: 1;
}

.hero-home-content {
  display: block;
  width: 90%;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  z-index: 10;
}
.hero-home-content h1 {
  display: block;
  color: #F3EEC8;
  font-size: 6em;
  font-weight: 700;
  line-height: 0.6em;
  text-transform: uppercase;
}
.hero-home-content h1 span {
  font-size: 0.7em;
}
.hero-home-content p {
  padding: 1em 0 0;
  font-size: 1.4em;
  color: #F3EEC8;
}/*# sourceMappingURL=hero-home.css.map */